import {
    Box,
} from '@material-ui/core';
import Grid from '@mui/material/Grid';
import React, {FunctionComponent, useEffect} from 'react';
import './grid-card.css';
import Cards from './assets/solana_student_card_x6_2.png';
import Bar from './assets/dot_bar.png';
import {ParallaxBanner, useParallax} from "react-scroll-parallax";
import DialogGirlImage from "./assets/solana_chan_live2d_v4.gif";

const GridCard: FunctionComponent = ()=>{

    useEffect(() => {

    });
    const card = useParallax<HTMLDivElement>({
        translateX: [-30, 15, 'easeOutQuint'],
        translateY: [50, -75, 'easeOutQuint'],
        scale: [1.8,1.8]
    });
    const bar = useParallax<HTMLDivElement>({
        translateX: [5, -5, 'easeOutQuint'],
        translateY: [-50, -100, 'easeOutQuint'],
        scale: [2,2]
    });

    const girl = useParallax<HTMLDivElement>({
        easing: 'easeOutQuad',
        translateX: [-15, 0],
        opacity: [-1, 1.5],
    });

    const dialog = useParallax<HTMLDivElement>({
        easing: 'easeOutQuad',
        translateX: [25, 0],
        opacity: [-1, 1.5],
    });

    return (

        <Grid container
                direction="column"
                justifyContent="center"
                alignItems="center"
                className="gridCardContainer">
            <Grid >
                <Grid container direction="column" className="girlContainer">
                    <div ref={girl.ref}>
                        <img src={DialogGirlImage} alt={"dialogGirl"}  className="dialogGirlImage"/>
                    </div>
                </Grid>
                <Grid container direction="column" className="dialogContainer">
                    <div ref={dialog.ref}>
                        <Box className="dialogContent" sx={{border: 1}}>
                            <p id="dialogTitle">10,000 Unique Solana Girls are waiting for you!</p>
                            <p style={{fontFamily: "FuturaHandwritten"}}>
                                Someday in 2022, the tokens in your wallet realised their own consciousness and become a Solana Girl (SOLG). You decided to send her to Solana Gakuen, developing her talent by joining lots of different activities and experience a happy school life. What incredible things are waiting for her?
                            </p>
                        </Box>
                    </div>
                </Grid>
                <Grid item xs={12} sx={{height: "50vw"}}>

                </Grid>
                <Grid className="cardsContainer">
                    <div  ref={card.ref} >
                        <img src={Cards} alt={"cards"} className="cards" />
                    </div>

                </Grid>
                <Grid className="barContainer">
                    <div  ref={bar.ref} >
                        <img src={Bar} alt={"bar"} className="bar" />
                    </div>
                </Grid>
            </Grid>
        </Grid>
    );
}

export default GridCard;